
@charset "UTF-8";
body {
  background-color: #f3f3f3;
}
body .weui-tabbar {
    background-color: #fff;
}
.main_container .main_typelist_box {
  height: 40px;
  position: relative;
  width: 560px;
  font-size: 0;
  border-bottom: 1px solid #f3f3f3;
  background-color: #fff;
}
.main_typelist_item {
  width: 70px;
  height: 40px;
  display: inline-block;
  text-align: center;
  line-height: 40px;
  font-size: 14px;
}
.main_typelist_bottomline {
  height: 2px;
  width: 70px;
  position: absolute;
  background-color: #2196f3;
  left: 0;
  bottom: 0;
  -webkit-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
}

/*  */
.main_typelist_item:nth-of-type(1)[class*="main_typelist_item--selected"] ~ .main_typelist_bottomline {
  left: 0px;
}
.main_typelist_item:nth-of-type(2)[class*="main_typelist_item--selected"] ~ .main_typelist_bottomline {
  left: 70px;
}
.main_typelist_item:nth-of-type(3)[class*="main_typelist_item--selected"] ~ .main_typelist_bottomline {
  left: 140px;
}
.main_typelist_item:nth-of-type(4)[class*="main_typelist_item--selected"] ~ .main_typelist_bottomline {
  left: 210px;
}
.main_typelist_item:nth-of-type(5)[class*="main_typelist_item--selected"] ~ .main_typelist_bottomline {
  left: 280px;
}
.main_typelist_item:nth-of-type(6)[class*="main_typelist_item--selected"] ~ .main_typelist_bottomline {
  left: 350px;
}
.main_typelist_item:nth-of-type(7)[class*="main_typelist_item--selected"] ~ .main_typelist_bottomline {
  left: 420px;
}
.main_typelist_item:nth-of-type(8)[class*="main_typelist_item--selected"] ~ .main_typelist_bottomline {
  left: 490px;
}
.main_typelist_item:nth-of-type(9)[class*="main_typelist_item--selected"] ~ .main_typelist_bottomline {
  left: 560px;
}
.main_typelist_item:nth-of-type(10)[class*="main_typelist_item--selected"] ~ .main_typelist_bottomline {
  left: 630px;
}
@-webkit-keyframes hongbao {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
25% {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
}
50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
75% {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
}
100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
}
@keyframes hongbao {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
25% {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
}
50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
75% {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
}
100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
}
.main_card_fixed {
  position: fixed;
  top: 50%;
  right: 10px;
  height: 50px;
  -webkit-animation: hongbao 0.5s ease-in 1s infinite;
          animation: hongbao 0.5s ease-in 1s infinite;
}
.main_card_fixed--select {
    -webkit-animation-name: nouu;
            animation-name: nouu;
}
.main_card_content {
  position: absolute;
  top: 43%;
  width: 60%;
  left: 50%;
  color: #fff;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.main_card_content > h3 {
    font-size: 27px;
    color: #fcec2b;
    text-align: center;
    margin-bottom: 10px;
}
.main_card_content > p {
    font-size: 12px;
}
.main_card_mask {
  height: 50%;
  width: 70%;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.main_card_mask > img {
    width: 100%;
}
.main_card_maskClose {
  height: 30px;
  width: 30px !important;
  position: absolute;
  right: 0;
  top: 0;
}
.main_container .weui-loadmore_line .weui-loadmore__tips {
  background-color: #f1f1f1 !important;
}
.main_swiper .vux-swiper-item {
  position: relative;
  width: 100%;
}
.main_swiper .vux-swiper-item img {
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.main_listbox {
  margin-top: 91px;
  width: 100%;
  height: calc(100% - 142px);
  overflow-y: auto;
  background-color: #f1f1f1;
  /* background-color: #f3f3f3; */
}
.main_listbox .vux-slider > .vux-indicator > a > .vux-icon-dot.active, .main_listbox .vux-slider .vux-indicator-right > a > .vux-icon-dot.active {
    background-color: #2196f3;
}
.main_listbox .list_compent_list_box {
    height: calc(100% - 142px);
    position: fixed;
    top: 91px;
    width: 100%;
    overflow-y: auto;
    padding-bottom: 40px;
}
.main_listbox .list_compent_list_box .weui-loadmore_line .weui-loadmore__tips {
      background-color: #f1f1f1 !important;
}
.main_typelist {
  position: fixed;
  top: 50px;
  left: 0;
  width: 100%;
}
.main_header {
  box-sizing: border-box;
  padding: 10px 20px;
  height: 50px;
  background-color: #fff;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
  border-bottom: 1px solid #f3f3f3;
}
.main_search {
  width: 85%;
  border-radius: 5px;
  background-color: #f5f5f5;
  box-sizing: border-box;
  font-size: 14px;
  color: #6d6d6d;
  height: 30px;
  line-height: 30px;
  -webkit-box-flex: 1;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}
.main_search > i {
    float: left;
    font-size: 20px;
    color: #bfbfbf;
    margin-left: 10px;
}
.main_menu {
  width: 40px;
  -webkit-box-flex: 0;
  -webkit-flex-grow: 0;
          flex-grow: 0;
  display: block;
  text-align: left;
  line-height: 30px;
  font-size: 25px;
  color: #bfbfbf;
}
.main_menu > i {
    font-size: 20px;
}
.main_hot, .main_recommend {
  background-color: #f6f6f6;
  height: 45px;
  border-bottom: 1px solid #dddddd;
  position: relative;
}
.main_hot > img, .main_recommend > img {
    display: block;
    margin: 0 auto;
    height: 100%;
}
.main_hot_more {
    position: absolute;
    right: 15px;
    display: block;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    background: #2196f3;
    border-radius: 50%;
    font-size: 16px;
    color: #fff;
    width: 20px;
    height: 20px;
    line-height: 18px;
    text-align: center;
}
.main_hot_list {
  background-color: #fff;
}
.main_hot_single {
  height: 130px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  border-bottom: 1px solid #f3f3f3;
}
.main_hot_img {
  height: 130px;
  width: 130px;
  display: block;
  -webkit-box-flex: 0;
  -webkit-flex-grow: 0;
          flex-grow: 0;
  box-sizing: border-box;
  padding: 15px;
}
.main_hot_img > img {
    height: 100px;
    width: 100px;
    display: block;
}
.main_hot_title {
  color: #666;
  font-size: 13px;
  overflow: hidden;
  text-overflow: ellipsis;
  font-weight: 400;
}
.main_hot_price {
  color: #f80000;
  font-size: 15px;
}
.main_hot_text {
  box-sizing: border-box;
  padding: 20px 10px;
  -webkit-box-flex: 1;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.main_recommend_list {
  background-color: #f1f1f1;
  margin-bottom: 20px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.main_recommend_single {
  background-color: #fff;
  width: 49.5%;
  margin-bottom: 5px;
  box-sizing: border-box;
  padding: 10px;
}
.main_recommend_img {
  display: block;
  box-sizing: border-box;
}
.main_recommend_img > img {
    margin: 0 auto;
    width: 100%;
    display: block;
}
.main_recommend_title {
  margin-top: 5px;
  color: #272727;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: 400;
}
.main_recommend_price {
  margin-top: 5px;
  color: #f80000;
  font-size: 15px;
}
.main_recommend_text {
  box-sizing: border-box;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
}



