
.Router {
     position: absolute;
     width: 100%;
     -webkit-transition: all .4s ease;
     transition: all .4s ease;
     top: 0;
     height: 100%;
}
.slide-left-enter,
 .slide-right-leave-active {
     opacity: 0;
    -webkit-transform: translate(100%, 0);
    transform: translate(100%, 0);
}
.slide-left-leave-active,
.slide-right-enter {
     opacity: 0;
    -webkit-transform: translate(-100%, 0);
    transform: translate(-100% 0);
}



